li {
    list-style: none;
}

a {
    text-decoration: none;
}

nav {
    z-index: 10001;
}


.navbar {
    padding: 10px 13%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

nav ul li {
    display: inline-block;
    padding: 1em;
}

nav ul li a {
    color: #495e57;
}

nav ul li a:hover {
    transition: background-color 0.5s, border-color 0.5s, color 0.5s;
    background-color: #F4CE14;
    padding: 20px;

}

nav ul li a:focus {
    background-color: #F4CE14;
}

.menu-icon {
    display: none;
    cursor: pointer;
}

.bar {
    width: 25px;
    height: 3px;
    background-color: black;
    margin: 5px 0;
}

.nav-links {
    list-style: none;
    display: flex;
    align-items: center;
}



.nav-links.visible {
    flex-direction: column;
    position: absolute;
    top: 40px;
    right: 0;
    width: 100%;
    background-color: #495E57;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: all 0.3s ease-in-out;
}

.active-link {
    background-color: #F4CE14;
    padding-top: 50%;
    padding-bottom: 50%;
    padding-left: 10px;
    padding-right: 10px;
}

.nav-links li:nth-child(4) .active-link,
.nav-links li:nth-child(5) .active-link {
    background-color: #F4CE14;
    padding-top: 30%;
    padding-bottom: 30%;
    padding-left: 10px;
    padding-right: 10px;
}





@media screen and (max-width: 1000px) {
    .menu-icon {
        display: block;
    }

    .nav-links {
        display: none;
    }

    .nav-links.visible {
        display: flex;
        background: #333333;
        z-index: 1000;
        padding: 20px 0;
    }

    .navbar {
        background: #fff;
        padding: 10px 8%;
    }

    nav ul li a {
        color: #fff;
    }

    .bar {
        background: #333333;
    }

    .banner-img {
        display: none;
    }

    .logo img {
        height: 50px;
    }

    .active-link {
        background-color: #F4CE14;
        padding-top: 30%;
        padding-bottom: 30%;
        padding-left: 10px;
        padding-right: 10px;
        border-radius: 5px;
    }
}

@media screen and (max-width: 768px) {
    .menu-icon {
        display: block;
    }

    .nav-links {
        display: none;
    }

    .nav-links.visible {
        display: flex;
        background: #333333;
        z-index: 1000;
        padding: 20px 0;
    }

    .navbar {
        background: #fff;
        padding: 10px 8%;
    }

    nav ul li a {
        color: #fff;
    }

    .bar {
        background: #333333;
    }

    .banner-img {
        display: none;
    }

    .logo img {
        height: 50px;
    }

    .active-link {
        background-color: #F4CE14;
        padding-top: 30%;
        padding-bottom: 30%;
        padding-left: 10px;
        padding-right: 10px;
        border-radius: 5px;
    }

}